import io from 'socket.io-client';

import store from '@/store';
import { SET_INCOMING_CALL_STATUS } from '@/store/socket/mutations';
import callStatuses from '@/store/socket/constants';
import { connectToRoom } from '@/services/room';

// const socket = io('/operators');
const socket = io.connect('/operators');

export function initImcomingCallsListening() {
  socket.on('incoming call', onIncomingCall);
}

function onIncomingCall(data) {
  console.log('incoming call', data);

  notifyAboutIncomingCall.then(onCallAccepted).catch(onCallDeclined);
}

function onCallAccepted() {
  socket.emit('accept call', { query: { identity: 'JohnLennon' } });
}

function onCallDeclined() {
  socket.emit('call declined');
}

// store interactors

function notifyAboutIncomingCall() {
  store.commit(SET_INCOMING_CALL_STATUS, callStatuses.INCOMING);
  return new Promise((resolve, reject) => {
    const unwatch = store.watch(
      state => state.dialogs.result,
      (value) => {
        unwatch();
        if (value === callStatuses.ACCEPTED) {
          resolve();
        } else {
          reject();
        }
      },
    );
  });
}
